import React from 'react';

import SEO from '../../components/SEO';

import avatar from '../../images/me.png';

import { useStaticQuery, graphql } from "gatsby";

import {
  AvatarSection,
  AvatarContainer,
  AvatarDisplay,
  AvatarFrame,
  AvatarImage,
  AvatarInfo,
  AvatarName,
  AvatarRole,
  AvatarSocialMedia,
  CarrerBadge,
  CarrerBadgeContainer,
  CarrerContainer,
  CarrerInnerContainer,
  CarrerItem,
  CarrerImgWrapper,
  CarrerName,
  CarrerImage,
  CarrerInfo,
  CarrerDate,
  CarrerCompany,
  CarrerLabel,
  CarrerSection,
  CarrerTimeline,
  CarrerTitle,
  CertificateSection,
  Container,
  CubeEffect,
  EducationSection,
  HomeWrapper,
  SkillSection,
  SkillTitle,
  SkillVersion,
  SocialMediaButton,
  TerminalCommand,
  TerminalCommandPrefix,
  TerminalCursor,
  TerminalDivider,
  TerminalResult
} from './styles';

export default function Home() {
  const data = useStaticQuery(graphql`
    query homePageQuery {
      site {
        siteMetadata {
          carrers { id, title, date, company, imgUrl },
          education { id, title, date, company, imgUrl },
          skills { id, date, title, user }
        }
      }
    }
  `);

  return (
    <HomeWrapper>
      <SEO title='Home' />
      {/* Avatar section */ }
      <AvatarSection id="home">
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/>
        <CubeEffect/> 
        <Container>
          <AvatarContainer>
            <AvatarDisplay>
              <AvatarFrame />
              <AvatarImage src={avatar} />
            </AvatarDisplay>
            <AvatarInfo>
              <AvatarName>Gabriel Mazurco</AvatarName>
              <AvatarRole>Desenvolvedor Web</AvatarRole>
              <AvatarSocialMedia>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
                <SocialMediaButton>
                  F
                </SocialMediaButton>
              </AvatarSocialMedia>
            </AvatarInfo>
          </AvatarContainer> 
        </Container>
      </AvatarSection>
      {/* Carrer section */}
      <CarrerSection id="carrer">
        <Container
          data-sal="slide-up"
          data-sal-delay="100"
          data-sal-duration="500"
          data-sal-easing="ease" 
        >
          <CarrerContainer>
            <CarrerTitle>Carreira</CarrerTitle>
            <CarrerInnerContainer>
              <CarrerLabel>
                Sou um profissional voltado para rea de programao Web, tenho como Stack principal
                a linguagem de programao <strong>Javascript</strong>. Utilizo frameworks para tornar
                o trabalho mais eficiente como: <strong>Vue, React, React Native, Gatsby, Node...</strong>{' '}
                Seguindo esse caminho, ja trabalhei nas seguintes empresas:
                <br/><br />
                <strong>Linguagens e Frameworks: </strong>
                <CarrerBadgeContainer>
                  <CarrerBadge>html</CarrerBadge>
                  <CarrerBadge>css</CarrerBadge>
                  <CarrerBadge>javascript</CarrerBadge>
                  <CarrerBadge>Java</CarrerBadge>
                  <CarrerBadge>Kotlin</CarrerBadge>
                  <CarrerBadge>node.js</CarrerBadge>
                  <CarrerBadge>Vue</CarrerBadge>
                  <CarrerBadge>React</CarrerBadge>
                  <CarrerBadge>Gatsby</CarrerBadge>
                  <CarrerBadge>Express</CarrerBadge>
                </CarrerBadgeContainer>
              </CarrerLabel>
              <CarrerTimeline>
                { data.site.siteMetadata.carrers.map(carrer =>  (
                    <CarrerItem key={carrer.id}>
                      <CarrerInfo>
                        <CarrerDate>{ carrer.date }</CarrerDate>
                        <CarrerName>{ carrer.title }</CarrerName>
                        <CarrerCompany>{ carrer.company }</CarrerCompany>  
                      </CarrerInfo>
                      <CarrerImgWrapper>
                        <CarrerImage src={carrer.imgUrl}></CarrerImage>
                      </CarrerImgWrapper>
                    </CarrerItem>
                  )
                ) }
              </CarrerTimeline>
            </CarrerInnerContainer>
          </CarrerContainer>
        </Container>
      </CarrerSection>
      {/* Skills section */}
      <SkillSection id="skill">
        <Container
          data-sal="slide-up"
          data-sal-delay="100"
          data-sal-duration="500"
          data-sal-easing="ease"
        >
          <SkillTitle>&nbsp;_&nbsp;&nbsp;&nbsp;_&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;_&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;_&nbsp;_&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;_&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;_&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</SkillTitle>
          <SkillTitle>|&nbsp;|&nbsp;|&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;|&nbsp;|&nbsp;&nbsp;&nbsp;(_)&nbsp;|&nbsp;&nbsp;&nbsp;|&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;|&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</SkillTitle>
          <SkillTitle>|&nbsp;|_|&nbsp;|&nbsp;__&nbsp;_|&nbsp;|__&nbsp;&nbsp;_|&nbsp;|&nbsp;__|&nbsp;|&nbsp;__&nbsp;_&nbsp;&nbsp;__|&nbsp;|&nbsp;___&nbsp;&nbsp;___&nbsp;</SkillTitle>
          <SkillTitle>|&nbsp;&nbsp;_&nbsp;&nbsp;|/&nbsp;_`&nbsp;|&nbsp;'_&nbsp;\|&nbsp;|&nbsp;|/&nbsp;_`&nbsp;|/&nbsp;_`&nbsp;|/&nbsp;_`&nbsp;|/&nbsp;_&nbsp;\/&nbsp;__|</SkillTitle>
          <SkillTitle>|&nbsp;|&nbsp;|&nbsp;|&nbsp;(_|&nbsp;|&nbsp;|_)&nbsp;|&nbsp;|&nbsp;|&nbsp;(_|&nbsp;|&nbsp;(_|&nbsp;|&nbsp;(_|&nbsp;|&nbsp;&nbsp;__/\__&nbsp;\</SkillTitle>
          <SkillTitle>\_|&nbsp;|_/\__,_|_.__/|_|_|\__,_|\__,_|\__,_|\___||___/</SkillTitle>
          <SkillVersion>v2020.04</SkillVersion>
          <TerminalDivider/>
          <TerminalCommandPrefix>
            mazurco@portfolio:~/Skills$
          </TerminalCommandPrefix>
          <TerminalCommand>
            {' '}ls -l
          </TerminalCommand>
          { data.site.siteMetadata.skills.map(skill => (
            <TerminalResult key={skill.id}>-rwxr-xr-x {skill.user} {skill.date} <strong>"{skill.title}"</strong></TerminalResult>
          ))}
          <TerminalCommandPrefix>
            mazurco@portfolio:~/Skills$
          </TerminalCommandPrefix>
          <TerminalCursor />
        </Container>
      </SkillSection>
      {/* Education section */}
      <EducationSection id="education">
        <Container
          data-sal="slide-up"
          data-sal-delay="100"
          data-sal-duration="500"
          data-sal-easing="ease" 
        >
          education
        </Container>
      </EducationSection>
      {/* Certificates section */}
      <CertificateSection id="certificate">
        <Container
          data-sal="slide-up"
          data-sal-delay="100"
          data-sal-duration="500"
          data-sal-easing="ease" 
        >
          certificates
        </Container>
      </CertificateSection>
    </HomeWrapper>
  );
}
